
<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示会员的商品购买咨询信息及商家回复</li>
      <li>可对不合规的信息进行修改、更正</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>商品名称</label>
        <el-input v-model="queryParams.productName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>咨询用户</label>
        <el-input v-model="queryParams.userName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>咨询时间</label>
        <cms-picker v-model="timer" @change="setTimer" ></cms-picker>
      </div>
      <div class="query-group">
        <label>回复状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="已回复" :value="1"></el-option>
          <el-option label="未回复" :value="0"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 数据列表 -->
    <table class="comment-table" v-loading="loading">
      <tbody v-for="(item,index) in tableData" :key="index">
        <tr class="comment-th">
          <td style="width:30%;padding-left:10px;">
           <span>咨询商品</span>
          </td>
          <td style="padding-right:10px;">
            <div class="consult-main-th">
              <span style="padding-left:10px;">咨询内容</span>
              <cms-button size="small" :colorPicker='true'
                @click.stop="$routerLink('/consult/update','update',{id:item.id})">修改内容
              </cms-button>
            </div>
          </td>
        </tr>
        <tr style="border:1px solid #EBEEF5;">
          <td class="comment-goods" style="border-right:1px solid #EBEEF5;width:40%">
            <!-- 商品信息 -->
            <div class="comment__inner">
              <img class="product-img" :src="$cms.filterUrl(item.product.coverImage)">
              <div class="product-title">
                <h3 class="comment-title-h">{{item.product.name}}</h3>
              </div>
            </div>
         </td>
         <td class="comment-item" style="width:72%">
           <!-- 咨询内容 -->
           <div class="comment-remark" style="border-bottom:1px solid #EBEEF5;">
             <div style="font-size:14px;">
               <p style="font-weight:bold;" v-if="item.member">{{item.member.username}}:</p>
               <p class="comment-title">{{item.content}}</p>
               <p>{{$moment(item.createTime)}}</p>
             </div>
           </div>
          <div class="comment-remark">
             <div  v-for="(info,indexs) in item.assocatedAnswer" :key="indexs"
               v-if="item.assocatedAnswer.length>0" :style="{'border-top':indexs>0?'1px solid #EBEEF5':''}">
               <p style="font-size:14px;">{{info.product.store.storeName}}&nbsp;&nbsp;回复：</p>
               <p class="comment-title">{{info.content}}</p>
               <p style="margin-top:6px;font-size:14px;">{{$moment(info.createTime)}}</p>
             </div>
             <p v-if="item.assocatedAnswer.length==0" class="consult-none" style="padding-top:20px;">暂无商家回复</p>
           </div>
         </td>
        </tr>
        <tr>
          <td colspan="2" style="height:20px;"></td>
        </tr>
      </tbody>
    </table>
    <div class="comment-empty" v-if="!loading&&tableData.length==0">暂无列表数据</div>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { consultPage } from '@/api/marketing/index.js'
var defaultData = {
  'productName': '',
  'userName': '',
  'storeName': '',
  'consultStartTime': '',
  'consultEndTime': '',
  'status': '',
  'page': 1,
  'size': 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      timer: []
    }
  },
  methods: {
    setTimer(timer) {
      this.queryParams.consultStartTime = timer[0]
      this.queryParams.consultEndTime = timer[1]
      this.query()
    }
  },
  created() {
    this.getTableData(consultPage, this.queryParams)
  }
}
</script>
<style scoped>
.comment-goods{
  width: 36%;
}
.consult-main-th{
  display:flex;
  justify-content:space-between;
  align-items: center;
}
.comment-title{
  max-width:650px;
  padding-left: 10px;
}
</style>



